import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {deleteReservation, registerReservation, updateReservation} from "../../reudx/reservationSlice";
import {closeMenuLayer, selectMenuLayer} from "../../reudx/menuLayerSlice";
import {ReservationInfo} from "../../types";

export default function MenuLayer() {

  const dispatch = useDispatch();
  const {isOpen, reservationInfo} = useSelector(selectMenuLayer);

  const [reservation, setReservation] = useState<ReservationInfo>(reservationInfo);
  const [isValidation, setIsValidation] = useState<boolean>(false)

  const handleRegisterClick = () => {
    const {name} = reservation
    if (name) {
      dispatch(registerReservation(reservation))
      handleClose()
    } else {
      alert(" .")
    }
  }

  const handleEditClick = () => {
    dispatch(updateReservation(reservation))
    handleClose()
  }

  const handleClose = () => {
    dispatch(closeMenuLayer())
  };

  const onChange = (e: any) => {
    setReservation({
      ...reservation,
      [e.target.id]: e.target.value,
    })
  }

  const handleDeleteClick = () => {
    dispatch(deleteReservation(reservation.id))
    dispatch(closeMenuLayer())
  }

  useEffect(() => {
    setReservation(reservationInfo)
  }, [reservationInfo])

  useEffect(() => {
    if (reservation.name) {
      setIsValidation(true)
    } else {
      setIsValidation(false)
    }

  }, [reservation.name])

  return (
    <div>
      <Dialog open={isOpen} onClose={handleClose} fullWidth={true}>
        <DialogTitle>{reservation.roomName}:   </DialogTitle>
        <DialogContent>
          <DialogContentText>
            
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label=""
            required
            type="text"
            fullWidth
            variant="standard"
            onChange={onChange}
            value={reservation.name || ''}
          />
          <DialogContentText>
             
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="purpose"
            label=""
            type="text"
            fullWidth
            variant="standard"
            onChange={onChange}
            value={reservation.purpose || ''}
          />
          <DialogContentText>
             
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="time"
            type="text"
            fullWidth
            variant="standard"
            disabled
            value={`${reservation.startTime} ~ ${reservation.endTime}`}
          />
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleClose} color={"warning"}></Button>
          {reservation.id && <Button variant="contained" onClick={handleDeleteClick} color={"error"}></Button>}
          {
            reservation.id ?
              <Button onClick={handleEditClick} color={"primary"} variant="contained"
                      disabled={!isValidation}></Button>
              :
              <Button onClick={handleRegisterClick} color={"primary"} variant="contained"
                      disabled={!isValidation}></Button>
          }
        </DialogActions>
      </Dialog>
    </div>
  );
}
